body {
  padding: 20px;
  min-height: 100vh;
}

footer {
  position: fixed;
  background-color: #ccc;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 8px;
  text-align: right;
}

.header .lead {
  max-width: 620px;
}

.gallery-item {
  width: 200px;
  padding: 15px;
}

.thumb {
  width: 150px;
  height: auto;
  margin-bottom: 20px;
  -webkit-box-shadow: 4px 4px 12px 0px rgba(34, 60, 80, 0.5);
  -moz-box-shadow: 4px 4px 12px 0px rgba(34, 60, 80, 0.5);
  box-shadow: 4px 4px 12px 0px rgba(34, 60, 80, 0.5);
}

.App {
  min-height: 100vh;
}

.font-face-ps {
 font-family: 'PoorStory';
}

/* ---Background--- */
@keyframes move {
    100% {
        transform: translate3d(0, 0, 1px) rotate(360deg);
    }
}

.background {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background: #09aafb;
    overflow: hidden;
    z-index: -1;
}

.background span {
    width: 45vmin;
    height: 45vmin;
    border-radius: 45vmin;
    backface-visibility: hidden;
    position: absolute;
    animation: move;
    animation-duration: 42;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}


.background span:nth-child(0) {
    color: #eec47c;
    top: 21%;
    left: 86%;
    animation-duration: 66s;
    animation-delay: -39s;
    transform-origin: 17vw -15vh;
    box-shadow: -90vmin 0 12.095013767349545vmin currentColor;
}
